import { Component, OnInit } from '@angular/core';
import { Location } from '@angular/common';
import * as projectsList from '../../../assets/json-files/projects.json';
import { Project } from 'src/app/model/project';
import { ActivatedRoute, Router } from '@angular/router';

@Component({
  selector: 'app-project-details',
  templateUrl: './project-details.component.html',
  styleUrls: ['./project-details.component.css']
})
export class ProjectDetailsComponent implements OnInit {
  project: Project;
  projects: Project[] = (projectsList as any).default;

  constructor(private _location: Location, private route: ActivatedRoute) { }

  ngOnInit(): void {

    // console.log(this.route.snapshot.paramMap.get('id'));
    // console.log(this.projects);
    this.loadData();
  }

  back(): void {
    this._location.back();
  }

  loadData(): void {
    this.project = this.projects.find(p=>p.id === this.route.snapshot.paramMap.get('id'));
    document.getElementById('project-desc').innerHTML = this.project.description;
    console.log(this.project);
  }

}
