import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { ContactMeComponent } from './contact-me/contact-me.component';
import { PfContentComponent } from './pf-content/pf-content.component';
import { PfSkillsComponent } from './pf-skills/pf-skills.component';
import { ProjectDetailsComponent } from './project-section/project-details/project-details.component';
import { ProjectsComponent } from './project-section/projects/projects.component';
import { TimeLineComponent } from './time-line/time-line.component';

const routes: Routes = [
  { path: '', component: PfContentComponent },
  { path: 'home', component: PfContentComponent },
  { path: 'skills', component: PfSkillsComponent },
  { path: 'history', component: TimeLineComponent },
  { path: 'contact', component: ContactMeComponent },
  { path: 'projects', component: ProjectsComponent },
  { path: 'project-details/:id', component: ProjectDetailsComponent }
  // { path: '**', component: TimeLineComponent }

];

@NgModule({
  // imports: [RouterModule.forRoot(routes, {
  //   useHash: true,
  //   anchorScrolling: 'enabled'
  // })],
  imports: [RouterModule.forRoot(routes, {scrollPositionRestoration: 'enabled'})],
  exports: [RouterModule]
})
export class AppRoutingModule { }
